import { Controller } from '@ali/4ever-cangjie';

const mergeData = (controller: Controller, data: Record<string, any>) => {
  const { value } = controller;

  const newData = Object.keys(data).reduce((tgt, key) => {
    const v = data[key];
    return {
      ...tgt,
      [key]: v,
    };
  }, value.data);

  return controller.setData(newData);
};

export default mergeData;
